<template>
  
  <select class="form-control form-control-sm skeleton-input" :id="'select_' + the_name" :name="the_name"
    v-model="value" @change="changeV" @focus="animateDom"
    :class="{ 'si-wait': wait, 'si-finish': finish }"    
  >
    <option v-for="(v, index) in items" :key="index" :value="index" >{{ v.named }}</option>
  </select>

</template>

<script>
import tool from '@/common/es/tool'

  export default {
    name: '',
    props: {
      the_name: {
        type: String
      },
      the_placeholder: {
        type: String
      },
      items: {
        type: Array
      },
      wait: {
        type: Boolean,
        default: true
      },
      finish: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        value: ''
      }
    },
    methods: {
      setV(e) {
        this.value = e
      },
      changeV() {
        this.$emit('change_Father', this.items[this.value])
      },
      animateDom(e) {
        if (e.type == 'focus') {
          this.$emit('write_Father')
        }
      }
    }
  }
</script>

<style lang="sass" scoped>
  
@import "@/common/sass/base.sass"

</style>


