<template>

      <tr class="text-left skeleton-row" :id="'writing_row_' + index" v-if="writ.show">
        <td width="6%" class="text-accent fa-pos-fix-table">
          <i class="fa fa-magic skeleton-magic-icon hand" 
            v-if="!writ.finish"
            @click="magic">
          </i>
          <span v-else>
            {{ index + 1 }}
          </span>
        </td>
        <td width="10%" >
          
          <skeleton-input :the_name="index + '_si_num'" :the_placeholder="'編號'"
            :value="num"
            :wait="writ.num"
            :finish="writ.finish"

            @change_Father="changeNum"
            @write_Father="writeNum"

            ref="si_num"
          ></skeleton-input>
        </td>
        <td width="20%" >
          
          <skeleton-select :the_name="index + '_ss_named'" :the_placeholder="'描述'"

            :wait="writ.wait"
            :finish="writ.finish"
            :items="abouts"

            ref="si_named"
            @change_Father="changeNamed"
            @write_Father="writeNamed"
          >
          </skeleton-select>

        </td>
        <td width="15%">
          <div class="of-visible" :class="{ 'skeleton-td-count': writ.finish }">
            
            <skeleton-input :the_name="index + '_si_count'" :the_placeholder="'數量'"
              :value="count"
              :wait="writ.count"
              :finish="writ.finish"

              @change_Father="changeCount"
              
              ref="si_count"
            ></skeleton-input>

          </div>
        </td>
        <td width="15%">
          <div class="of-visible" :class="{ 'skeleton-td-price': writ.finish }">
            <skeleton-input :the_name="index + '_si_price'" :the_placeholder="'單價'"
              :value="price"
              :wait="writ.price"
              :finish="writ.finish"

              @change_Father="changePrice"
              @finish_Father="finishTrigger"
              ref="si_price"
            ></skeleton-input>
          </div>
        </td>
        <td width="9%" class="text-center">
          
        </td>
        <td width="15%" class="text-center skeleton-td-result">

          <div class="flex-center">
            {{ result }}
          </div>
        </td>
        
        <td width="10%" class="text-center skeleton-td-option">

          <div class="flex-center" v-if="writ.finish">
            <i class="fa hand listing-table-icon fa-trash-o" @click="itemOption('remove')"></i>
          </div>
        </td>
      </tr>

</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'

import SkeletonInput from '@/components/Input/Skeleton/SkeletonInput.vue'
import SkeletonSelect from '@/components/Input/Skeleton/SkeletonSelect.vue'
  export default {
    components: {
      SkeletonInput, SkeletonSelect
    },
    props: {
      origins: {
        type: Array
      },
      freights: {
        type: Array
      },
      writ: {
        type: Object
      },
      index: {
        type: Number
      },
      saves: {
        type: Array
      }
    },
    name: '',
    data() {
      return {
        id: '',
        num: '',
        named: '',
        count: '',
        price: '',
        unit: 0,

        abouts: [ ]
      }
    },
    mounted() {
      
    },
    computed: {
      result() {
        if (this.writ.finish) return tool.floatNum.floatMul(this.count, this.price)
      }
    },
    methods: {
      inList(n, L) {
        return tool.inList(n, L)
      },
      judgeData() {
        let res = true
        if ((this.num == '') || (this.num == undefined) || (this.num == null)) { res = false }
        if ((this.named == '') || (this.named == undefined) || (this.named == null)) { res = false }
        if ((this.count == '') || (this.count == undefined) || (this.count == null)) { 
          if (res) {
            tool.showToast('檢測到該產品數量為 0', 1920)
          }
          res = false
        }
        if ((this.price == '') || (this.price == undefined) || (this.price == null)) { res = false }
        
        if (res) { 
          return {
            id: this.id,
            num: this.num,
            named: this.named,
            count: this.count,
            price: this.price,
            unit: this.unit,
            res: tool.floatNum.floatMul(this.count, this.price),
            zk: ''
          } 
        }
        return false
      },
      finishTrigger() {
        const e = this.judgeData()
        
        if (e) {

          const can = this.saves.findIndex(item => item.id === e.id)
          if (can < 0) {
            this.writ.finish = true
            this.saves.push(e)

            // tool.showToast('產品【' + e.named + '】已錄入。', 920)
            this.$emit('finish_Father', this.index, e.id)
          }
        }
      },

      havent() {
        const o = this.origins.map(e => {
          return e.id
        })

        const s = this.saves.map(e => {
          return e.id
        })

        let a = new Set(o)
        let b = new Set(s)
        let res = new Set([...a].filter(x => !b.has(x)))

        return Array.from(res)
      },
      magic() {
        
        const e = this.havent()

        if (e.length < 1) {
          tool.showToast('報價單內的產品已全部添加，暫無遺漏。')
        } else {
          let now = e[0]

          now = this.origins.filter(e => e.id === now)[0]

          this.$refs.si_num.setV(now.num)
          this.changeNum(now.num)
          
          setTimeout(() => {
            tool.focus('select_' + this.index + '_ss_named')
          }, 300)
        }
      },

      // 加載產品 值
      introData(item) {
        this.named = item.named
        this.id = item.id

        this.num = item.num

        if (item.count) { this.count = item.count } else { this.count = 0 }
        this.price = item.price

        this.unit = item.unit

        //
        this.$refs.si_num.setV(this.num)
        this.$refs.si_count.setV(this.count)
        this.$refs.si_price.setV(this.price)
      },

      // 加載 選擇框
      loadingAbouts() {

        return new Promise((rev, rej) => {
          this.$refs.si_named.setV('')
          const res = []

          this.origins.map(e => {
            if (e.num == this.num) { res.push(e) }
          })
          if (res.length < 1) {
            this.freights.map(e => {
              if (e.num == this.num) { res.push(e) }
            })
          }
          rev(res)
        })
      },

      // 編號獲取焦點
      writeNum() {

        // 創建新行
        this.$emit('buildWriting_Father', this.index)
      },

      // 錄入蔬菜

      writeNamed() {
        if (this.abouts.length < 1) {
          tool.showToast('未找到相關產品', 1200)
        } else {
          this.introData(this.abouts[0])
        }
      },

      // 編號值改變
      async changeNum(e) {
        this.num = e
        this.abouts = await this.loadingAbouts()
        
        this.$refs.si_named.setV(0)
      },
      changeNamed(e) {
        const can = this.saves.findIndex(item => item.id === e.id)
        
        if (can < 0) {
          this.introData(e)
        } else {
          this.price = ''
          this.count = ''

          tool.showToast('已經錄入了【' + e.named + '】產品了，請更改編號')
        }

      },
      changeCount(e) {
        this.count = e
      },
      changePrice(e) {
        this.price = e
      },

      // 選取控制
      itemOption() {
        this.$emit('itemOption_Father', this.id, 'remove', this.index)
        const ind = this.saves.findIndex(item => item.id === this.id)
        this.saves.splice(ind, 1)
      }

    }
  }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"
@import "@/common/sass/choise.sass"
@import "@/common/sass/content.sass"

.plus-extra-bar
    background: #f7f7f7
    display: table


</style>


