<template>
    
    <input type="text" class="form-control form-control-sm skeleton-input" :id="'input_' + the_name" :name="the_name"
        :placeholder="the_placeholder"
        v-model="value" @change="changeV" @focus="animateDom" @blur="finishTrigger"
        :class="{ 'si-finish': finish, 'si-wait': !finish }"    
    >

</template>

<script>
    export default {
        props: {
            the_name: {
                type: String
            },
            the_placeholder: {
                type: String
            },
            wait: {
                type: Boolean,
                default: true
            },
            finish: {
                type: Boolean,
                default: false
            }
        },
        name: '',
        data() {
            return {
                value: ''
            }
        },
        methods: {
            setV(e) {
                this.value = e
            },
            changeV() {
                this.$emit('change_Father', this.value)
            },
            animateDom(e) {
                if (e.type == 'focus') {
                    this.$emit('write_Father')
                }
            },
            finishTrigger() {
                
                this.$emit('finish_Father')
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


