<template>
  <div class="row">
    <div class="col-sm-3">

      <h5 class="fw-none text-accent">其他信息</h5>
      <listing-prices-card :_prices="the_prices" :is_icon="true" @changeSignal="changePrices" v-if="the_prices!=null"></listing-prices-card>
      <div v-else class="member-loading-wrapper">
        <loading-middle></loading-middle>
      </div>

    </div>
    <div class="col-sm-3 pt-4">
      
      <div class="fs-14 timed-wrapper">
        <simple-input 
          @returnValue="reciverNumed" 
          :the_name="'listing_numd'"
          :the_tip_header="`發票編號`"
          :the_placeholder="`- -`"
          :the_min_length="3"
          :the_max_length="12"
          
          ref="listingNumInput"
          >
          <div slot="input_txt" @click="loadingNumed">發票編號：</div>
        </simple-input>
      </div>
      <br/>
      <div class="fs-14 timed-wrapper">
        <simple-input 
          @returnValue="reciverTimed" 
          :the_name="'listing_timed'"
          :the_tip_header="`發票時間`"
          :the_placeholder="`yyyy-mm-dd`"
          
          ref="listingTimeInput"
          >
          <div slot="input_txt" @click="loadingTimed">發票日期：</div>
        </simple-input>
      </div>

    </div>
    <div class="col-sm-3 pt-4">
      
      <div class="fs-14 timed-wrapper">
        <simple-select 
          @returnValue="reciverPayment" 
          :the_name="'listing_payment'" 
          :the_list="payments"

          ref="listingPaymentInput"
        >
          <div slot="input_txt">支付方式：</div>
        </simple-select>
      </div>
      <br/>
      <div class="fs-14 timed-wrapper">
        <simple-select 
          @returnValue="reciverPayTime" 
          :the_name="'listing_paytime'" 
          :the_list="pay_times"

          ref="listingPayTimeInput"
        >
          <div slot="input_txt">付款期限：</div>
        </simple-select>
      </div>

    </div>
    <div class="col-sm-1 flex-center">
      <button @click="_pricesSubmit" id="pricesResBtn" class="hidden"></button>
    </div>
    <div class="col-sm-2 pt-2">
      <button class="btn btn-light btn-block btn-sm hidden" 
        @click="addIntoFreight">
        <span class="fs-16">+</span>
        加入產品
      </button>
      <button class="btn btn-outline-info btn-block btn-sm"
        @click="pricesPdfView">下一步</button>

    </div>
  </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import SimpleInput from '@/components/Input/Simple/SimpleInput.vue'
import ListingMemberFaceCard from '@/components/Card/ListingMemberFaceCard.vue'
import LoadingMiddle from '@/components/Shimmer/LoadingMiddle.vue'

import conn from '@/service/conn'
import ListingPricesCard from '@/components/Card/ListingPricesCard.vue'
import SimpleSelect from '@/components/Input/Simple/SimpleSelect.vue'

export default {
  name: 'ListingPlusFooter',
  components: {
    SimpleInput,
    ListingMemberFaceCard,
    LoadingMiddle,
    ListingPricesCard,
    SimpleSelect
  },
  props: {
    the_prices: {
      type: Object
    }
  },
  data() {
    return {
      timed_listing: '',
      numed_listing: '',
      payment_listing: 0,
      paytime_listing: 0,

      pay_times: [

      ],
      pay_times_origin: [

      ],
      payments: [
        
      ]
    }
  },
  created() {
    this.payments = view.show_paymet()
  },
  async mounted() {
    await this.loadingNumed()
    await this.loadingTimed()
    await this.loadingPayTime()
  },
  methods: {
    _pricesSubmit() {
      this.$emit('pricesSubmit_Father')
    },
    pricesSubmit() {
      tool.click('pricesResBtn')
    },
    // 
    addIntoFreight() {
      this.$emit('openFreightInsert_Father')
    },
    pricesPdfView() {
      this.$emit('pricesPdfView_Father')
    },
    reciverTimed(v) {
      this.timed_listing = v
      // this.$emit('reciverTimed_Father', this.timed_listing)
    },
    reciverNumed(v) {
      this.numed_listing = v

      conn.listing.getOneByAny({
        num: v
      }).then(res => {
        if (conn.judge_response(res[0])) {

          this.$emit('reciverNumed_Father', '')
          tool.showToast('發票編號“' + v + '”重復，數據庫中已有相同編號的發票！')
          
        } else {
          // this.$emit('reciverNumed_Father', this.numed_listing)
        }
      })

    },
    reciverPayment(v) {
      this.payment_listing = v
    },
    reciverPayTime(v) {
      this.paytime_listing = v
    },
    validateInput() {
      let res = ''
      if (this.timed_listing == '') {
        res = '請檢查發票時間的輸入！'
      }
      if (this.numed_listing == '') {
        res = '請檢查發票編號的輸入！'
      }
      if (res != '') { 
        tool.showToast(res) 
      } else {
        return {
          timed: this.timed_listing,
          numed: this.numed_listing,
          payment: this.payment_listing,
          pay_time_id: this.pay_times_origin[this.paytime_listing].id,
          pay_time_named: this.pay_times_origin[this.paytime_listing].named
        }
      }

      return false
    },
    //
    changePrices() {
      tool.showModal('sureChangePricesModal')
    },

    // 自动生成 字数
    async loadingNumed() {
      const res = await conn.listing_no.getTodayNumed()
      
      this.$refs.listingNumInput.value = res
      this.numed_listing = res
    },

    // 自动生成时间
    async loadingTimed() {
      const res = tool.timed.getNumedTimed('-')
      this.timed_listing = res 
      this.$refs.listingTimeInput.value = res
    },

    // 定位 付款期限
    locationPayTime(named) {
      this.pay_times_origin.forEach((e, i) => {
        if (e.named == named) this.$refs.listingPayTimeInput.locationData(i) 
      })
    },
    async loadingPayTime() {
      const res = await conn.pay_time.getList({}, true)

      if (res) {
        this.pay_times_origin = res
        this.pay_times = res.map(e => e.named)

        if (this.the_prices) this.locationPayTime(this.the_prices.pay_time.named)
      }
    }
    
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

.timed-wrapper
  display: inline-block
  line-height: 1
  height: 24px

.member-loading-wrapper
  height: 70%
</style>


